<template>
  <component :is="tag" class="pl-cell-item" @click.stop="handleLink">
    <div class="pl-cell-item__header">
      <div class="icon-img" v-if="isUrl">
        <img :src="src" alt="image" v-if="src">
        <pl-svg-icon :icon="icon" v-if="icon" :size="size" />
      </div>
      <div
        v-if="title"
        :style="alignWay"
        :class="['pl-cell-item__title', classes.titleClass]"
        v-html="title">
      </div>
      <!--  -->
      <div v-if="$slots.desc" style="max-width: 80%">
        <slot name="desc"></slot>
      </div>
      <!--  -->
      <div
        v-if="value"
        :class="['pl-cell-item__value', classes.valueClass]"
        @click="handleValue(itemIndex)"
        v-html="value">
      </div>
      <!--  -->
      <div v-if="$slots.right" class="pl-cell-item__value">
        <slot name="right"></slot>
      </div>
      <div v-if="isLink && !$slots.right" class="pl-cell-item__value">
        <pl-svg-icon icon="arrow-right" size="18" />
      </div>
    </div>
    <div class="pl-cell-item__desc" v-if="desc" v-html="desc"></div>
  </component>
</template>

<script>

export default {
  name: 'pl-cell-item',
  props: {
    tag: {
      type: String,
      default: 'div'
    },
    src: { // 
      type: String,
      default: ''
    },
    icon: { // svg
      type: String,
      default: ''
    },
    size: {
      type: [Number, String],
      default: 24
    },
    title: { // 
      type: String,
      default: ''
    },
    value: { // 
      type: String,
      default: ''
    },
    desc: { // 
      type: String,
      default: ''
    },
    titleClass: { // 
      type: String,
      default: ''
    },
    valueClass: { // 
      type: String,
      default: ''
    },
    itemIndex: Number, // ncell-item
    titleAlign: { // , 
      type: String,
      default: 'left'
    },
    isLink: {
      type: Boolean,
      default: false
    },
    to: { // 
      type: String,
      default: ''
    }
  },
  computed: {
    classes () { // 
      return {
        titleClass: this.titleClass,
        valueClass: this.valueClass
      }
    },
    isUrl () { // svgicon
      return this.src || this.icon
    },
    alignWay () { // 
      return {
        textAlign: this.titleAlign
      }
    }
  },
  methods: {
    handleLink () {
      if (this.isLink) {
        this.$router.push(this.to)
      }
    },
    handleValue (itemIndex) { // 
      this.$emit('clickVal', itemIndex)
    }
  }
}
</script>

<style lang="less" scoped>
@import './style.less';
</style>
