<template>
  <div class="pl-collapse">
    <div class="pl-collapse__content" v-html="value" :style="styles"></div>
    <div class="pl-collapse__icon" @click="handleTrigger" :style="toWay">
      <span v-if="isChara">{{isTrigger ? unfoldText : packUpText}}</span>
      <pl-svg-icon :icon="iconName" size="12" />
    </div>
  </div>
</template>

<script>

export default {
  name: 'pl-collapse',
  props: {
    value: {
      type: String,
      default: ''
    },
    isChara: { // 
      type: Boolean,
      default: true
    },
    unfoldText: {
      type: String,
      default: ''
    },
    packUpText: {
      type: String,
      default: ''
    },
    align: { // 
      type: String,
      default: ''
    }
  },
  computed: {
    iconName () { // 
      return this.isTrigger ? 'arrow_d' : 'arrow_u'
    },
    styles () { // 
      return {
        'display': this.isTrigger ? '-webkit-box' : 'block'
      }
    },
    toWay () { // 
      return {
        'text-align': !this.isChara ? 'center' : this.align ? this.align : 'left'
      }
    }
  },
  data () {
    return {
      isTrigger: true
    }
  },
  methods: {
    handleTrigger () { // 
      this.isTrigger = !this.isTrigger
    }
  }
}
</script>

<style lang="less" scoped>
@import './style.less';
</style>
